:root {
  --bg-color: #f0f4f8;
  --board-bg: #ffffff;
  --cell-border: #cbd5e0;
  --water-color: #ebf8ff;
  --ship-color: #4a5568;
  --hit-color: #f56565;
  --miss-color: #a0aec0;
  --active-highlight: #4299e1;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--bg-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 20px;
}

h1 {
  color: #2d3748;
  margin-bottom: 30px;
}

/* Screen Management */
.screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1000px;
}

.hidden {
  display: none !important;
}

/* Boards Container */
#boards-container {
  display: flex;
  gap: 50px;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 20px;
}

.board-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Player Names Display */
.board-wrapper h2 {
  font-size: 1.5rem;
  color: #4a5568;
  margin-bottom: 15px;
  padding: 8px 20px;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.name {
  text-align: center;
  margin: 0.8rem;
  font-size: 1.2rem;
  width: 25%;
}

.active-turn {
  background-color: var(--active-highlight);
  color: white !important;
  box-shadow: 0 4px 6px rgba(66, 153, 225, 0.4);
}

/* Gameboards and Ships */
ship-container {
  display: flex;
  flex-direction: column;
  gap: 10px; 
  padding: 10px;
  border: 1px solid #ccc;
  width: 200px; 
}

.ship {
  display: flex;
  flex-direction: row;
  gap: 2px;
  padding: 5px;
  transition: all 0.2s ease-in-out;
  cursor: grab;
}

.ship.vertical {
  flex-direction: column; 
  width: fit-content;
}

.ship-cell {
  width: 40px;
  height: 40px;
  background-color: #4a5568;
  border: 1px solid #2d3748;
}

.ship.selected {
  outline: 3px solid #4299e1;
  background-color: rgba(66, 153, 225, 0.1);
  border-radius: 4px;
  width: fit-content;
}

.game-board {
  display: grid;
  grid-template-columns: repeat(10, 40px);
  grid-template-rows: repeat(10, 40px);
  background-color: var(--board-bg);
  border: 3px solid #2d3748;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}

.cell {
  width: 40px;
  height: 40px;
  border: 1px solid var(--cell-border);
  background-color: var(--water-color);
  cursor: crosshair;
  transition: background-color 0.2s;
}

.cell:hover {
  background-color: #bee3f8;
}

.cell.placement-preview {
    background-color: rgba(66, 153, 225, 0.4) !important;
    outline: 2px dashed #4299e1;
    z-index: 10;
}

.cell.placement-preview.invalid {
    background-color: rgba(245, 101, 101, 0.4) !important;
    outline-color: #f56565;
}

.cell.ship-present {
  background-color: var(--ship-color);
}

.cell.miss {
  background-color: var(--miss-color);
  position: relative;
}

.cell.miss::after {
  content: "â€¢";
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
}

.cell.hit {
  background-color: var(--hit-color);
  border-color: #c53030;
}

/* UI Elements: Buttons & Messages */
#message-area {
  text-align: center;
  margin-top: 30px;
  font-size: 1.25rem;
  font-weight: bold;
  color: #2d3748;
  height: 1.5em;
}

button {
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  color: white;
  background-color: #4299e1;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin: 5px;
  transition: background-color 0.2s;
}

button:hover {
  background-color: #3182ce;
}

/* Input Styles */
input[type="text"].name-input {
  padding: 10px;
  font-size: 1rem;
  border: 2px solid var(--cell-border);
  border-radius: 6px;
  margin: 0.8rem;
}

/* The Full-Screen Overlay */
#pass-device-cover {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(26, 32, 44, 0.95);
  backdrop-filter: blur(8px); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

/* The Modal Box inside the cover */
.modal-content {
  background-color: white;
  padding: 40px 60px;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.2);
  max-width: 400px;
}

.modal-content h2 {
  color: #2d3748;
  margin-bottom: 20px;
  font-size: 1.8rem;
}

/* Ready Button */
#ready-btn {
  background-color: #48bb78;
  color: white;
  border: none;
  padding: 15px 30px;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.2s, background-color 0.2s;
}

#ready-btn:hover {
  background-color: #38a169;
  transform: scale(1.05);
}

#ready-btn:active {
  transform: scale(0.95);
}
