a, and Safari.\n */\n\nabbr[title] {\n  border-bottom: none; /* 1 */\n  text-decoration: underline; /* 2 */\n  text-decoration: underline dotted; /* 2 */\n}\n\n/**\n * Add the correct font weight in Chrome, Edge, and Safari.\n */\n\nb,\nstrong {\n  font-weight: bolder;\n}\n\n/**\n * 1. Correct the inheritance and scaling of font size in all browsers.\n * 2. Correct the odd `em` font sizing in all browsers.\n */\n\ncode,\nkbd,\nsamp {\n  font-family: monospace, monospace; /* 1 */\n  font-size: 1em; /* 2 */\n}\n\n/**\n * Add the correct font size in all browsers.\n */\n\nsmall {\n  font-size: 80%;\n}\n\n/**\n * Prevent `sub` and `sup` elements from affecting the line height in\n * all browsers.\n */\n\nsub,\nsup {\n  font-size: 75%;\n  line-height: 0;\n  position: relative;\n  vertical-align: baseline;\n}\n\nsub {\n  bottom: -0.25em;\n}\n\nsup {\n  top: -0.5em;\n}\n\n/* Embedded content\n   ========================================================================== */\n\n/**\n * Remove the border on images inside links in IE 10.\n */\n\nimg {\n  border-style: none;\n}\n\n/* Forms\n   ========================================================================== */\n\n/**\n * 1. Change the font styles in all browsers.\n * 2. Remove the margin in Firefox and Safari.\n */\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n  font-family: inherit; /* 1 */\n  font-size: 100%; /* 1 */\n  line-height: 1.15; /* 1 */\n  margin: 0; /* 2 */\n}\n\n/**\n * Show the overflow in IE.\n * 1. Show the overflow in Edge.\n */\n\nbutton,\ninput { /* 1 */\n  overflow: visible;\n}\n\n/**\n * Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 1. Remove the inheritance of text transform in Firefox.\n */\n\nbutton,\nselect { /* 1 */\n  text-transform: none;\n}\n\n/**\n * Correct the inability to style clickable types in iOS and Safari.\n */\n\nbutton,\n[type=\"button\"],\n[type=\"reset\"],\n[type=\"submit\"] {\n  -webkit-appearance: button;\n}\n\n/**\n * Remove the inner border and padding in Firefox.\n */\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n  border-style: none;\n  padding: 0;\n}\n\n/**\n * Restore the focus styles unset by the previous rule.\n */\n\nbutton:-moz-focusring,\n[type=\"button\"]:-moz-focusring,\n[type=\"reset\"]:-moz-focusring,\n[type=\"submit\"]:-moz-focusring {\n  outline: 1px dotted ButtonText;\n}\n\n/**\n * Correct the padding in Firefox.\n */\n\nfieldset {\n  padding: 0.35em 0.75em 0.625em;\n}\n\n/**\n * 1. Correct the text wrapping in Edge and IE.\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove the padding so developers are not caught out when they zero out\n *    `fieldset` elements in all browsers.\n */\n\nlegend {\n  box-sizing: border-box; /* 1 */\n  color: inherit; /* 2 */\n  display: table; /* 1 */\n  max-width: 100%; /* 1 */\n  padding: 0; /* 3 */\n  white-space: normal; /* 1 */\n}\n\n/**\n * Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n\nprogress {\n  vertical-align: baseline;\n}\n\n/**\n * Remove the default vertical scrollbar in IE 10+.\n */\n\ntextarea {\n  overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10.\n * 2. Remove the padding in IE 10.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n  box-sizing: border-box; /* 1 */\n  padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n  -webkit-appearance: textfield; /* 1 */\n  outline-offset: -2px; /* 2 */\n}\n\n/**\n * Remove the inner padding in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n  -webkit-appearance: button; /* 1 */\n  font: inherit; /* 2 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in Edge, IE 10+, and Firefox.\n */\n\ndetails {\n  display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n  display: list-item;\n}\n\n/* Misc\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10+.\n */\n\ntemplate {\n  display: none;\n}\n\n/**\n * Add the correct display in IE 10.\n */\n\n[hidden] {\n  display: none;\n}\n\n/** RESET END **/\n\n:root {\n  --bg-color: #f0f4f8;\n  --board-bg: #ffffff;\n  --cell-border: #cbd5e0;\n  --water-color: #ebf8ff;\n  --ship-color: #4a5568;\n  --hit-color: #f56565;\n  --miss-color: #a0aec0;\n  --active-highlight: #4299e1;\n}\n\nbody {\n  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n  background-color: var(--bg-color);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  margin: 0;\n  padding: 20px;\n}\n\nh1 {\n  color: #2d3748;\n  margin-bottom: 30px;\n}\n\n/* Screen Management */\n.screen {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  width: 100%;\n  max-width: 1000px;\n}\n\n.hidden {\n  display: none !important;\n}\n\n/* Boards Container */\n#boards-container {\n  display: flex;\n  gap: 50px;\n  justify-content: center;\n  flex-wrap: wrap;\n  margin-top: 20px;\n}\n\n.board-wrapper {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n/* Player Names Display */\n.board-wrapper h2 {\n  font-size: 1.5rem;\n  color: #4a5568;\n  margin-bottom: 15px;\n  padding: 8px 20px;\n  border-radius: 8px;\n  transition: all 0.3s ease;\n}\n\n.name {\n  text-align: center;\n  margin: 0.8rem;\n  font-size: 1.2rem;\n  width: 25%;\n}\n\n.active-turn {\n  background-color: var(--active-highlight);\n  color: white !important;\n  box-shadow: 0 4px 6px rgba(66, 153, 225, 0.4);\n}\n\n/* Gameboards and Ships */\nship-container {\n  display: flex;\n  flex-direction: column;\n  gap: 10px; \n  padding: 10px;\n  border: 1px solid #ccc;\n  width: 200px; \n}\n\n.ship {\n  display: flex;\n  flex-direction: row;\n  gap: 2px;\n  padding: 5px;\n  transition: all 0.2s ease-in-out;\n  cursor: grab;\n}\n\n.ship.vertical {\n  flex-direction: column; \n  width: fit-content;\n}\n\n.ship-cell {\n  width: 40px;\n  height: 40px;\n  background-color: #4a5568;\n  border: 1px solid #2d3748;\n}\n\n.ship.selected {\n  outline: 3px solid #4299e1;\n  background-color: rgba(66, 153, 225, 0.1);\n  border-radius: 4px;\n  width: fit-content;\n}\n\n.game-board {\n  display: grid;\n  grid-template-columns: repeat(10, 40px);\n  grid-template-rows: repeat(10, 40px);\n  background-color: var(--board-bg);\n  border: 3px solid #2d3748;\n  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);\n}\n\n.cell {\n  width: 40px;\n  height: 40px;\n  border: 1px solid var(--cell-border);\n  background-color: var(--water-color);\n  cursor: crosshair;\n  transition: background-color 0.2s;\n}\n\n.cell:hover {\n  background-color: #bee3f8;\n}\n\n.cell.placement-preview {\n    background-color: rgba(66, 153, 225, 0.4) !important;\n    outline: 2px dashed #4299e1;\n    z-index: 10;\n}\n\n.cell.placement-preview.invalid {\n    background-color: rgba(245, 101, 101, 0.4) !important;\n    outline-color: #f56565;\n}\n\n.cell.ship-present {\n  background-color: var(--ship-color);\n}\n\n.cell.miss {\n  background-color: var(--miss-color);\n  position: relative;\n}\n\n.cell.miss::after {\n  content: \"â€¢\";\n  color: white;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  font-size: 24px;\n}\n\n.cell.hit {\n  background-color: var(--hit-color);\n  border-color: #c53030;\n}\n\n/* UI Elements: Buttons & Messages */\n#message-area {\n  text-align: center;\n  margin-top: 30px;\n  font-size: 1.25rem;\n  font-weight: bold;\n  color: #2d3748;\n  height: 1.5em;\n}\n\nbutton {\n  padding: 12px 24px;\n  font-size: 1rem;\n  font-weight: 600;\n  color: white;\n  background-color: #4299e1;\n  border: none;\n  border-radius: 6px;\n  cursor: pointer;\n  margin: 5px;\n  transition: background-color 0.2s;\n}\n\nbutton:hover {\n  background-color: #3182ce;\n}\n\n/* Input Styles */\ninput[type=\"text\"].name-input {\n  padding: 10px;\n  font-size: 1rem;\n  border: 2px solid var(--cell-border);\n  border-radius: 6px;\n  margin: 0.8rem;\n}\n\n/* The Full-Screen Overlay */\n#pass-device-cover {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  background-color: rgba(26, 32, 44, 0.95);\n  backdrop-filter: blur(8px); \n  display: flex;\n  justify-content: center;\n  align-items: center;\n  z-index: 9999;\n}\n\n/* The Modal Box inside the cover */\n.modal-content {\n  background-color: white;\n  padding: 40px 60px;\n  border-radius: 12px;\n  text-align: center;\n  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.2);\n  max-width: 400px;\n}\n\n.modal-content h2 {\n  color: #2d3748;\n  margin-bottom: 20px;\n  font-size: 1.8rem;\n}\n\n/* Ready Button */\n#ready-btn {\n  background-color: #48bb78;\n  color: white;\n  border: none;\n  padding: 15px 30px;\n  font-size: 1.2rem;\n  font-weight: bold;\n  border-radius: 8px;\n  cursor: pointer;\n  transition: transform 0.2s, background-color 0.2s;\n}\n\n#ready-btn:hover {\n  background-color: #38a169;\n  transform: scale(1.05);\n}\n\n#ready-btn:active {\n  transform: scale(0.95);\n}\n"],"sourceRoot":""}]);
